  .sidebar {
    min-height: calc(100vh - 5.5625rem);
    z-index: 1000;
    /* Behind the navbar */
    border-right: 2px solid #e9e9e9;
  }
  .sidebar-sticky {
    max-height: calc(100vh - (4.5625rem + 1px)) !important;
    padding: 2.1875rem 1.875rem;
    overflow-x: hidden;
    overflow-y: hidden;
    /* Scrollable contents if viewport is shorter than content. */
  }
  .sidebar-sticky:hover {
    overflow-x: hidden;
    overflow-y: auto;
    /* Scrollable contents if viewport is shorter than content. */
  }
  .sidebar-sticky .nav:last-child {
    margin-bottom: 0;
  }
}

/*
* Content main area of documentation
*/
#main {
  padding: 3.125rem 1.8125rem;
}

@media (min-width: 768px) {
  #main {
    padding: 2.1875rem 5rem;
  }
}

.file-structure {
  position: relative;
}

.file-structure:before {
  content: ' ';
  width: 1px;
  height: calc(100% - 1.875rem);
  background-color: #e9e9e9;
  position: absolute;
  left: 0.625rem;
  top: 50%;
  z-index: -1;
  transform: translateY(-50%);
}

.file-structure li {
  margin-bottom: 0.9375rem;
}

.file-structure li ul {
  position: relative;
  list-style-type: none;
  padding: 0 0 0 1.875rem;
  margin: 0.9375rem 0;
}

.file-structure li ul:before {
  content: ' ';
  width: 1px;
  height: calc(100% - 1.875rem);
  background-color: #e9e9e9;
  position: absolute;
  left: 2.5rem;
  top: 50%;
  z-index: -1;
  transform: translateY(-50%);
}
